<template>
<!-- 人才库 -->
    <div class="home-module">
        <h3 class="module-title">
            <span>人才库</span>
            <div class="opt-bar">
                <el-button size="mini" @click="$router.push({name:'resume'})">查看更多 <i class="el-icon-more"></i></el-button>
            </div>
        </h3>
        <div class="home-panel-inner">
            <table class="module-table-list">
                <tbody>
                    <tr v-for="(item,i) in talentData" :key="i" @click="$router.push({name:'resumeDetail'})">
                        <td>姓名：{{item.name || '-'}}</td>
                        <td>毕业时间：{{item.graduateDate || '-'}}</td>
                        <td>专业:{{item.speciality || '-'}}</td>
                        <td>学历：{{item.qualifications || '-'}}</td>
                        <td>更新时间{{item.updateTime || '-'}}</td>
                    </tr>
                </tbody>
            </table>
        </div>
        
        
    </div>
</template>
<script>
import {getList} from '@/api/home'
export default {
    name:'talentPool',
    data(){
        return {
            talentData:[
                {
                    name:'里斯',
                    graduateDate:'2020-9',//毕业时间   
                    speciality:'环保学',//专业     
                    qualifications:'本科',//学历
                },{}
            ]
        }
    },
    mounted(){
        this.getCvList()
    },
    methods:{
        getCvList(){
            getList().then(res=>{
                
                if(res.code === 200) {
                    this.talentData = res.rows
                }else{
                    this.$message.error(res.msg);
                }
            })
        }
    }
}
</script>


