<template>
    <div>
        <!-- 搜索栏 start -->
        <div class="search-wrap">
            <label class="search-box">
                <input type="text" placeholder="快速搜索岗位">
                <button @click="search">搜索</button>
            </label>
        </div>
        <!-- 搜索栏 end -->
        <!-- 推荐职位 start -->
        <div class="home-panel home-module">
            <h3 class="module-title">
                <span>推荐职位</span>
                <div class="opt-bar">
                    <el-button size="mini" icon="el-icon-refresh">换一换</el-button>
                    <el-button size="mini" icon="el-icon-more" @click="$router.push({name:'JobFair'})">查看更多</el-button>
                </div>
            </h3>
            <ul class="recommended-list">
                <li v-for="(item,i) in recommendData" :key="i" @click="$router.push({name:'JobDetail'})">
                    <div class="jobs-info">
                        <h4>{{item.positionName}}</h4>
                        <div class="list">
                            <span>{{item.salaryBegin}}~{{item.salaryEnd}}</span>
                            <span>{{item.workCity}}</span>
                            <span>{{item.eduReq}}</span>
                            <span>{{item.expReq}}</span>
                        </div>
                    </div>
                    <div class="company-info">
                        <h4>{{item.comName}}</h4>
                        <div class="list">
                            <span class="info-tag">年度旅游</span>
                            <span class="info-tag">通讯津贴</span>
                            <span class="info-tag">年度旅游</span>
                            <span class="info-tag">通讯津贴</span>
                            <span class="info-tag">年度旅游</span>
                            <span class="info-tag">通讯津贴</span>
                        </div>
                    </div>
                    <span class="triangle-tag"><i>企</i></span>
                </li>
            </ul>
        </div>
        <!-- 推荐职位 end -->
        <div class="home-panel">
            <industry-information></industry-information>
            <talent-pool></talent-pool>
        </div>
        <!-- 专家库 -->
        <expert-database></expert-database>
        <div class="home-panel">
            <technical-communication></technical-communication>
            <entrepreneurship-competition></entrepreneurship-competition>
        </div>
        <enterprise-style></enterprise-style>
        <friend-link></friend-link>
    </div>
</template>
<script>
import IndustryInformation from './IndustryInformation'
import TalentPool from './talentPool'
import ExpertDatabase from './expertDatabase'
import TechnicalCommunication from './technicalCommunication'
import EntrepreneurshipCompetition from './entrepreneurshipCompetition'
import EnterpriseStyle from './enterpriseStyle'
import FriendLink from './friendLink';
import {getPosition,getUserInfo} from '@/api/home'
// import axios from 'axios';
export default {
    name:"HomePage",
    components:{
        IndustryInformation,
        TalentPool,
        ExpertDatabase,
        TechnicalCommunication,
        EntrepreneurshipCompetition,
        EnterpriseStyle,
        FriendLink
    },
    data(){
        return{
            recommendData:[
                {
                    comName:'XXXXX公司BBB',//公司名称
                    positionName:'主播',//职位名称
                    workCity:'北京',//工作区域
                    salaryBegin:8,
                    salaryEnd:12,
                    eduReq:'大专',
                    expReq:'1-3年'
                },{},{}
            ],
        }
    },
    computed:{
    },
    mounted(){
        getUserInfo();
        // 获取推荐职位
        this.getPositionFun();
    //     this.$axios
    //     .get('http://192.168.31.254:8080/env/position/getPositionById/1')
    //   .then(res => {
    //       console.log(res)
    //   })
    },
    methods:{
        search(){
            this.$router.push({name:'JobFair'})
        },
        // 获取推荐职位
        getPositionFun(){
            getPosition().then(res => {
                console.log(res);
                if(res.code === 200) {
                    this.recommendData = res.rows
                }else{
                    this.$message.error(res.msg);
                }
            })
        },
    }
}
</script>


