<template>
<!--    友情链接 -->
    <div class="home-panel home-module">
        <h3 class="module-title">
            <span>友情链接</span>
        </h3>
        <ul class="friend-list">
            <li v-for="(item,i) in linkData" :key="i"><img src="https://iph.href.lu/180x80?fg=666666&bg=cccccc"/></li>
            
        </ul>
        <ul class="friend-text-list">
            <li v-for="(item,i) in linkData" :key="i">兰州市环境保护局</li>
        </ul>
        
    </div>
</template>
<script>
export default {
    name:"friendLink",
    data(){
        return {
            linkData:[
                {},{}
            ]
        }
    }
}
</script>
<style lang="less" scoped>
.friend-list {
    overflow: hidden;
    padding: 10px 24px;
    li {
        display: block;
        float: left;
        width: 180px;
        height: 80px;
        margin: 10px 25px;
        img {
            width: 100%;
            height: 100%;
        }
    }
}
.friend-text-list {
    overflow: hidden;
    padding: 0 49px 10px;
    li {
        display: block;
        float: left;
        margin-right: 10px;
        cursor: pointer;
        line-height: 18px;
        &:hover {
            text-decoration: underline;
            color: #12bbb8;
        }
    }
}
</style>


