import Vue from 'vue'
import "babel-polyfill"
import Router from 'vue-router'
import HomePage from '@/views/homePage/index'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'HomePage',
      component: HomePage,
    },
    // 简历库
    {
      path:"/resume",
      name:"resume",
      component:()=>import ('@/views/resume/index.vue')
    },
    // 发布职位
    {
      path:"/release",
      name:"release",
      component:()=>import ('@/views/positionManage/release.vue')
    },
    // 职位管理
    {
      path:"/positionManage",
      name:"positionManage",
      component:()=>import ('@/views/positionManage/index.vue')
    },
    // 简历详情
    {
      path:"/resume/detail",
      name:"resumeDetail",
      component:()=>import ('@/views/resume/detail.vue')
    },
    // 企业信息
    {
      path:"/companyInfo",
      name:"companyInfo",
      component:()=>import ('@/views/companyInfo.vue')
    },
    // 编辑简历
    {
      path:"/editResume",
      name:"editResume",
      component:()=>import ('@/views/editResume.vue')
    },
    {
      path:"/JobFair",
      name:"JobFair",
      component:()=>import ('@/views/jobFair.vue')
    },
    // 企业列表
    {
      path:"/CompanyFair",
      name:"CompanyFair",
      component:()=>import ('@/views/companyFair.vue')
    },
    // 企业详情
    {
      path:"/CompanyDetail",
      name:"CompanyDetail",
      component:()=>import ('@/views/companyDetail.vue')
    },
    {
      path:"/JobDetail",
      name:"JobDetail",
      component:()=>import ('@/views/JobDetail.vue')
    },
    {
      path:"/Talents",
      name:"Talents",
      component:()=>import ('@/views/talents.vue')
    },
    {
      path:"/ScholarlyMeeting",
      name:"ScholarlyMeeting",
      component:()=>import ('@/views/scholarlyMeeting.vue')
    },
    {
      path:"/NewsDetail",
      name:"NewsDetail",
      component:()=>import ('@/views/newsDetail.vue')
    },
    {
      path:"/TalentsDetail",
      name:"TalentsDetail",
      component:()=>import ('@/views/talentsDetail.vue')
    },
    {
      path:"/Login",
      name:"Login",
      component:()=>import ('@/views/login.vue')
    },
    {
      path:"/Regist",
      name:"Regist",
      component:()=>import ('@/views/regist.vue')
    },
    {
      path:"/ComplitInfo",
      name:"ComplitInfo",
      component:()=>import ('@/views/complitInfo.vue')
    }
  ]
})
export default router;


