<template>
  <div class="header-warp">
    <div class="inner">
      <h3 class="logo">甘肃省环保专业大学生就业创业平台</h3>
      <!-- 菜单 start -->
      <ul class="nav-list">
        <li v-for="(item, index) in personList" :key="index">
          <router-link :to="item.url">{{ item.name }}</router-link>
        </li>
      </ul>
      <!-- 菜单 end -->
      <div class="opt-bar">
        <template v-if="userId">
          <a href="javascript:void(0)" @click="lookResume"
            >我的简历</a
          >
          <el-popover placement="bottom" width="100" trigger="hover">
            <a href="javascript:void(0)" slot="reference">招聘数据</a>
            <ul class="popover-list">
              <li @click="jumpRouter('resume')">简历库</li>
              <li @click="jumpRouter('positionManage')">职位管理</li>
            </ul>
          </el-popover>
          <el-popover placement="bottom" width="100" trigger="hover">
            <a href="javascript:void(0)" slot="reference">*****公司</a>
            <ul class="popover-list">
              <li @click="jumpRouter('companyInfo')">企业信息</li>
              <li>退出登录</li>
            </ul>
          </el-popover>
        </template>

        <template v-else>
          <router-link to="/Login">我要找工作</router-link>
          <router-link to="/Regist">我要招聘</router-link>
        </template>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "Header",
  data() {
    return {
      personList: [
        { name: "首页", url: "/" },
        { name: "人才招聘", url: "/JobFair" },
        { name: "学术交流", url: "/ScholarlyMeeting" },
        { name: "优秀人才", url: "/Talents" },
        { name: "企业", url: "/CompanyFair" },
      ],
    };
  },
  computed: {
    ...mapState(["userId"]),
  },
  mounted() {
    console.log(this.$store);
  },
  methods: {
    jumpRouter(path, param) {
        this.$router.push({ name: path });
    },
    lookResume(){
        this.$router.push({ name: 'resumeDetail',params:{'userId':this.userId} });
    }
  },
};
</script>


