<template>
<!-- 企业风采 -->
    <div class="home-panel home-module">
        <h3 class="module-title">
            <span>企业风采</span>
        </h3>
        <ul class="enterprise-list">
            <li v-for="(item,i) in enterpriseData" :key="i" @click="$router.push({name:'CompanyDetail'})">
                <div class="img-wrap"><img :src="item.comPhotoUrl" alt=""></div>
                <h4>{{item.comName}}</h4>
                <p>{{item.industry}} / 融资 / {{item.comNumPeople}} 人</p>
            </li>
        </ul>
        
    </div>
</template>
<script>
import {getCompanyList} from '@/api/home'
export default {
    name:"enterpriseStyle",
    data(){
        return {
            enterpriseData:[
            ]
        }
    },
    mounted(){
        getCompanyList().then(res => {
            console.log(res,"1111")
            if(res.code === 200) {
                this.enterpriseData = res.rows
            }else{
                    this.$message.error(res.msg);
            }
        })
    }
}
</script>


